<template>
  <div class="sync-video">
    <section class="cms-list-query" style="padding-top:0">
      <div class="query-group">
        <cms-button size="small" type="success" @click="syncData">同步视频素材</cms-button>
      </div>
      <div class="query-group">
        <cms-button icon='up' size='small' type="success" @click="videoVisible=true">上传视频素材</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' ref='dataTable'>
      <el-table-column prop="request.name" label="名称" min-width="400">
        <template slot-scope="scope">

          <div class="wechat-video__name">
            {{scope.row.request.name}}
          </div>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" min-width="220">
        <template slot-scope="scope">
          {{$moment(scope.row.wechatUpdateTime*1000)}}
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='320' fixed="right" align="right">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click="downloadVideo(scope.row.id)">下载</cms-button>
          <cms-button icon='error' size='small' type='info' @click="deleteData(scope.row.id,scope.row.mediaId)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
    <el-dialog title="上传视频素材" :visible="videoVisible" :before-close="close" width="800px">
      <div class='videoUpload'>
        <el-form :model="formData" style="padding:15px;" label-width="120px" ref="form">
          <el-form-item label="视频标题" prop="title" :rules="[
            { required: true, message: '必须填写一个视频标题'}]">
            <el-input v-model="formData.title" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="视频描述" prop="introduction"
            :rules="[ { required: true, message: '请输入视频描述'}]" >
            <el-input v-model="formData.introduction" type="textarea" maxlength="200"></el-input>
          </el-form-item>
          <el-form-item label="视频" prop="uploadFile"  class="media-btn-wrap" :rules="[
            { required: true, message: '必须上传一个视频',trigger: 'change' }]">
            <cms-upload :on-change='getUpload' type='video'>
              <el-button icon='up' type="primary" size='mini'>选择视频</el-button>
            </cms-upload>
            <div>{{uploadFileName}}</div>
          </el-form-item>
        </el-form>
        <div class="el-dialog__footer">
          <el-button @click="saveVideo" type="primary" size="mini">确定</el-button>
          <el-button @click="close" type='info' size="mini">取消</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import list from '@/mixins/list'
import {
  wechatMaterialPage,
  wechatMaterialSync,
  wechatMaterialDelete,
  wechatDownLoadVideo,
  materialVideoUpload
} from '@/api/wechat/index.js'
export default {
  mixins: [list],
  data() {
    return {
      queryParams: {
        name: '',
        type: 'video',
        page: 1,
        size: 10
      },
      videoVisible: false,
      formData: {
        title: '',
        introduction: '',
        uploadFile: ''
      },
      uploadFileName: ''
    }
  },
  methods: {
    syncData() {
      const loading = this.$loading({
        target: '.sync-video',
        text: '正在同步素材...'
      })
      wechatMaterialSync({ type: 'video' }).then(res => {
        loading.close()
        if (res.code === 200) {
          this.getTableData(wechatMaterialPage, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    deleteData(id, mediaId) {
      // 删除素材
      this.$confirm('确定删除此素材?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        wechatMaterialDelete({ id: id, media_id: mediaId }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.getTableData(wechatMaterialPage, this.queryParams)
          } else {
            this.$errorMessage('删除失败,' + res.message)
          }
        })
      })
    },
    downloadVideo(id) {
      wechatDownLoadVideo({ id: id }).then(res => {
        if (res.code === 200) {
          location.href = res.data
        }
      })
    },
    getUpload(files) {
      this.uploadFileName = files[0].name
      this.formData.uploadFile = files[0]
      this.$refs['form'].clearValidate(['uploadFile'])
    },
    saveVideo() {
      this.$refs['form'].validate(valid => {
        if (valid) {
          const params = new FormData()
          params.append('title', this.formData.title)
          params.append('introduction', this.formData.introduction)
          params.append('uploadFile', this.formData.uploadFile)
          const loading = this.$loading({
            target: '.videoUpload',
            text: '正在上传...'
          })
          materialVideoUpload(
            params,
            res1 => {},
            res2 => {
              loading.close()
              if (res2.code === 200) {
                this.close()
                this.$successMessage('上传成功')
                this.getTableData(wechatMaterialPage, this.queryParams)
              } else {
                this.$errorMessage(res2.message)
              }
            }
          )
        } else {
          return false
        }
      })
    },
    close() {
      this.formData.title = ''
      this.formData.introduction = ''
      this.formData.uploadFile = ''
      this.uploadFileName = ''
      this.videoVisible = false
      this.$refs['form'].clearValidate()
    }
  },
  created() {
    this.getTableData(wechatMaterialPage, this.queryParams)
  }
}
</script>





