<template>
  <div class="sync-image-text">
    <section class="cms-list-query"  style="padding-top:0">
      <div class="query-group">
        <cms-button size="small" type="success" @click="syncNewsData">同步图文素材</cms-button>
      </div>
      <div class="query-group">
        <cms-button  icon='add'  size="small" type="success" 
        @click="$routerLink('/wechatMaterial/imageText/save','save')">新增图文素材</cms-button>
      </div>
    </section>
    <!--  图文卡片 -->
    <div class="we-card__list" v-loading='loading'>
      <div class="we-card__warpper" v-for="(content) in tableData" :key="content.id">
        <template v-for="(item,index) in content.requestArray">
          <div class="we-card__item we-card__item--thumb" v-if="index==0" :key="index">
            <a :href="item.url" target="_blank" class="we-card__link">
              <img class="we-card__img" :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
              <span class="we-card__title--thumb" v-if="item.title!=''">
                {{item.title}}
              </span>
            </a>
            <div class="we-card-mask__preview">
              <a :href="item.url" target="_blank">预览文章</a>
            </div>
          </div>
          <div class="we-card__item  we-card__item--sub" v-else :key="index">
            <a :href="item.url" target="_blank" class="we-card__link">
              <div class="we-card__sub--inner">
                <div class="we-card__sub--img">
                  <img :src=" $cms.weChatUrl(item.thumbMediaUrl)" alt="">
                </div>
                <div class="we-card__sub--title">
                  {{item.title}}
                </div>
              </div>
            </a>
            <div class="we-card-mask__preview">
              <a :href="item.url" target="_blank">预览文章</a>
            </div>
          </div>
        </template>
        <div class="we-card__footer">
          <span class="fl">
            更新时间 {{$cms.getDateDiff(content.wechatUpdateTime*1000)}}
          </span>
          <span class="fr">
            <i class="el-icon-edit-outline" @click="editData(content.mediaId)"></i>
            <i class="el-icon-delete" @click="deleteData(content.id,content.mediaId)"></i>
          </span>
        </div>
      </div>
    </div>
    <cms-pagination @change="getPage" :total='total' style="padding-top:0;"></cms-pagination>
  </div>
</template>

<script>
import list from '@/mixins/list'
import {
  wechatMaterialPage,
  wechatMaterialSyncNews,
  wechatMaterialDelete

} from '@/api/wechat/index.js'
export default {
  mixins: [list],
  data() {
    return {
      queryParams: {
        name: '',
        type: 'news',
        page: 1,
        size: 10
      }
    }
  },
  methods: {
    editData(mediaId) {
      this.$routerLink(
        '/wechatMaterial/imageText/save',
        'update',
        {
          mediaId: mediaId
        }
      )
    },
    deleteData(id, mediaId) {
      // 删除素材
      this.$confirm('确定删除此素材?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        wechatMaterialDelete({ id: id, media_id: mediaId }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.getTableData(wechatMaterialPage, this.queryParams)
          } else {
            this.$errorMessage('删除失败,' + res.message)
          }
        })
      })
    },
    syncNewsData() { // 同步素材
      const loading = this.$loading({
        target: '.sync-image-text',
        text: '正在同步素材...'
      })
      wechatMaterialSyncNews().then(res => {
        loading.close()
        if (res.code === 200) {
          this.getTableData(wechatMaterialPage, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData(wechatMaterialPage, this.queryParams)
  }
}
</script>





