<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" ref='productTab'>
      <el-tab-pane label="图文消息" name="first">
        <image-text></image-text>
      </el-tab-pane>
     <el-tab-pane label="图片" name="second" lazy>
        <image-list></image-list>
      </el-tab-pane>
       <el-tab-pane label="语音" name="third" lazy>
        <audio-list></audio-list>
      </el-tab-pane>
       <el-tab-pane label="视频" name="fourth" lazy>
        <video-list></video-list>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import imageText from './imageText.vue'
import imageList from './imageList.vue'
import audioList from './audioList.vue'
import videoList from './videoList.vue'
export default {
  components: {
    imageText,
    imageList,
    audioList,
    videoList
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {}

}
</script>

<style>
</style>



