<template>
  <div class="sync-image">
    <div style="margin:0 auto;text-align: center;">
      <el-radio-group v-model="resourceStatus" size="small">
        <el-radio-button :label="1">微信</el-radio-button>
        <el-radio-button :label="2">服务器</el-radio-button>
      </el-radio-group>
    </div>

    <!-- 微信资源 -->
    <div v-show="resourceStatus==1">
      <section class="cms-list-query" style="padding-top:0">
        <div class="query-group">
          <cms-button size="small" type="success" @click="syncNewsData">同步图片素材</cms-button>
        </div>
        <div class="query-group">
          <cms-upload :on-change='getUpload'>
            <cms-button icon='up' size='small' type="success">上传微信图片</cms-button>
          </cms-upload>
        </div>
      </section>
      <!-- 微信图片和服务器图片切换 -->
      <div class="cms-picture-zone__body wechat-picture-zone__body">
        <ul class="zone--items--flex" ref="picItems">
          <li class="zone--items" v-for="(item,index) in tableData" :key="index">
            <div class="body--img-box" @click="previewImg($cms.weChatUrl(item.request.url),item.request.name)">
              <img :src="
              item.request.isLocal ? item.request.url : $cms.weChatUrl(item.request.url)" alt="item.request.name" :title="item.request.name">
            </div>
            <div class="body--img-name">
              <span>{{item.request.name}}</span>
            </div>
            <div class="upload-cover" v-if="item.request.isAdd">
              <el-progress type="circle" :percentage="item.request.percent" class="cover-progress" :status="item.request.status?'success':'exception'"></el-progress>
            </div>
          </li>
        </ul>
      </div>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
    <!-- 服务端图片 -->
    <div v-show="resourceStatus==2">
      <!--图片列表-->
      <picture-zoom></picture-zoom>
    </div>
  </div>
</template>
<script>
import list from '@/mixins/list'
import pictureZoom from './components/pictureZoom'
import {
  wechatMaterialPage,
  wechatMaterialSync,
  materialUpload
} from '@/api/wechat/index.js'

export default {
  mixins: [list],
  components: {
    pictureZoom
  },
  data() {
    return {
      queryParams: {
        name: '',
        type: 'image',
        page: 1,
        size: 10
      },
      resourceStatus: 1 // 服务器端微信端资源切换
    }
  },
  methods: {
    getUpload(fileList) {
      // 获取到上传的文件
      for (const key in fileList) {
        if (fileList.hasOwnProperty(key)) {
          const obj = {
            request: {
              url: window.URL.createObjectURL(fileList[key]),
              name: fileList[key].name,
              isAdd: true,
              status: true,
              isLocal: true
            }
          }
          this.tableData.unshift(obj)
          this.uploadFile(fileList[key], key)
        }
      }
    },
    uploadFile(file, index) {
      // file 上传的文件对象 index 批量上传确定是第几个 上传的自定义实现
      const param = new FormData() // 创建form对象
      const type = 'image'
      param.append('uploadFile', file, file.name) // 通过append向form对象添加数据
      param.append('type', type)
      materialUpload(
        param,
        res1 => {
          // 处理上传进度的显示
          const loaded = res1.loaded
          const total = res1.total
          const percent = Math.round(loaded / total * 100)
          this.$set(this.tableData[index].request, 'percent', percent) // 给每一个上传项赋值,这里就会有上传的效果了
        },
        res2 => {
          if (res2.code === 200) {
            this.$set(this.tableData[index].request, 'status', true) // 判断进度成功
            setTimeout(() => {
              this.$set(this.tableData[index].request, 'isAdd', false) // 成功去掉遮罩
            }, 1000)
            this.getTableData(wechatMaterialPage, this.queryParams)
          } else {
            this.$set(this.tableData[index].request, 'status', false) // 判断进度失败
            this.$errorMessage(res2.message)
            setTimeout(() => {
              this.tableData.splice(index, 1)
            }, 1000)
          }
        }
      )
    },
    syncNewsData() {
      const loading = this.$loading({
        target: '.sync-image',
        text: '正在同步素材...'
      })
      wechatMaterialSync({ type: 'image' }).then(res => {
        loading.close()
        if (res.code === 200) {
          this.getTableData(wechatMaterialPage, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    previewImg(url, name) {
      // 图片预览
      this.$viewer({
        name: name,
        url: url
      })
    }
  },
  created() {
    this.getTableData(wechatMaterialPage, this.queryParams)
  }
}
</script>







