<template>
  <div class="sync-audio">
    <section class="cms-list-query"  style="padding-top:0">
      <div class="query-group">
        <cms-button size="small" type="success" @click="syncData">同步音频素材</cms-button>
      </div>
      <div class="query-group">
        <cms-upload :on-change='getUpload' class="audioUpload">
          <cms-button icon='up' size='small' type="success">上传音频素材</cms-button>
        </cms-upload>
      </div>
    </section>
    
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' ref='dataTable'>
      <el-table-column prop="request.name" label="名称" min-width="220">
      </el-table-column>
      <el-table-column label="创建时间" min-width="220">
        <template slot-scope="scope">
          {{$moment(scope.row.request.update_time)}}
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='320' fixed="right"  align="right">
        <div slot-scope="scope">
            <cms-button  size='small' type='info' @click="downData(scope.row,scope.$index)">
              {{palyerIndex==scope.$index?'停止播放':'播放'}}
            </cms-button>
           <cms-button icon='error' size='small' type='info'  @click="deleteData(scope.row.id,scope.row.mediaId)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  <audio :src="audioSrc"  autoplay="autoplay" ref="audio"></audio>
  
  </div>
</template>

<script>
import list from '@/mixins/list'
import {
  wechatMaterialPage,
  wechatMaterialSync,
  wechatMaterialDelete,
  materialUpload,
  wechatDownLoadMaterail
} from '@/api/wechat/index.js'
export default {
  mixins: [list],
  data() {
    return {
      queryParams: {
        name: '',
        type: 'voice',
        page: 1,
        size: 10
      },
      fileName: '', // 文件
      audioSrc: null,
      palyerIndex: -1
    }
  },
  methods: {
    getUpload(files) { // 获取到上传的文件
      this.fileName = files[0]
      this.uploadFile()
    },
    uploadFile(file, index) { // 上传文件
      const params = new FormData()// 创建formData对象
      params.append('type', 'voice')// 添加所需要的对象
      params.append('uploadFile', this.fileName)
      const loading = this.$loading({
        target: '.audioUpload',
        text: '正在上传...'
      })
      materialUpload(params, res1 => {}, res2 => {
        loading.close()
        if (res2.code === 200) {
          this.$successMessage('上传成功')
          this.getTableData(wechatMaterialPage, this.queryParams)
        } else {
          this.$errorMessage(res2.message)
        }
      }
      )
    },
    syncData() {
      const loading = this.$loading({
        target: '.sync-audio',
        text: '正在同步素材...'
      })
      wechatMaterialSync({ type: 'voice' }).then(res => {
        loading.close()
        if (res.code === 200) {
          this.getTableData(wechatMaterialPage, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    deleteData(id, mediaId) { // 删除素材
      this.$confirm('确定删除此素材?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        wechatMaterialDelete({ id: id, media_id: mediaId }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.getTableData(wechatMaterialPage, this.queryParams)
          } else {
            this.$errorMessage('删除失败,' + res.message)
          }
        })
      })
    },
    downData(data, index) {
      const audio = this.$refs.audio
      if (index === this.palyerIndex) {
        audio.pause()
        this.palyerIndex = -1
      } else {
        audio.pause()
        if (data.audioSrc) {
          this.audioSrc = data.audioSrc
          this.palyerIndex = index
          audio.play()
        } else {
          wechatDownLoadMaterail({ id: data.id }).then(res => {
            const blob = new Blob([res])
            this.audioSrc = window.URL.createObjectURL(blob)
            this.$set(this.tableData[index], 'audioSrc', window.URL.createObjectURL(blob))
            this.palyerIndex = index
          })
        }
      }
    }
  },
  created() {
    this.getTableData(wechatMaterialPage, this.queryParams)
  }
}
</script>





